// app/components/MovieList.tsx
import { Movie } from '@/types';
import Image from 'next/image';

interface MovieListProps {
  movies: Movie[];
  onMovieSelect: (movie: Movie) => void;
}

export default function MovieList({ movies, onMovieSelect }: MovieListProps) {
  return (
    <div>
      <h2 className="text-white text-2xl font-semibold mb-4 px-8">Upcoming Movies</h2>
      <div className="flex gap-4 overflow-x-auto px-8 pb-6">
        {movies.map((movie) => (
          <div 
            key={movie.id}
            onClick={() => onMovieSelect(movie)}
            className="flex-none cursor-pointer transition-transform hover:scale-105"
          >
            {/* Added position-relative here to fix the warning */}
            <div className="relative w-[180px] h-[270px] rounded-lg overflow-hidden">
              <Image
                src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                alt={movie.title}
                fill
                className="object-cover"
                priority
                sizes="180px"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}